@import './_reset.scss';

$mainColor : #ff0000;

.red {
  color: $mainColor;
} 

.my-alert {
  background: #eeeeee;
  padding: 15px;
  border-radius: 5px;
  max-width: 500px;
  width: 100%;
  margin: auto;
  // test
//  box-sizing: border-box;
}

.my-alert p {
  margin-bottom: 0;
}

.my-alert-red {
  @extend .my-alert;
  background: #ffe591;
}


